import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
//import { DashboardApi }  from '../../shared/loopback_sdk';
import {Subscription} from 'rxjs';
import {CookieService} from 'angular2-cookie/core';
import { ShareService } from '../../providers/share-service/share-service';
import { LoopBackConfig, LoopBackAuth, App_usersApi } from '../../shared/loopback-sdk';
declare var sdkPrefix;
@Component({
  templateUrl: 'dashboard.component.html'
})
export class DashboardComponent implements OnInit {
	busy:  any;
	rolename:any;
	username:any;
	loading:boolean=true;
	constructor(private _cookieService:CookieService,private router:Router,public sharS:ShareService, private loopAuth:LoopBackAuth, private uApi:App_usersApi) {
		let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this.uApi.getUserRole(loginFlagToken).subscribe(
			data=>{
				this.loading = false;
        //console.log("data", data);
				this.rolename = data.result[0].name;
				this.username = data.result[0].realm;
			},
			(err)=>{},
			()=>{}
		)
	}

  ngOnInit(): void{

  }
}



